.rt_slides {
	outline: 1px solid green;
	overflow: hidden;

	& * {
		box-sizing: border-box;
	}
}

.rt_wrapper {
	display: flex;
	transition: 0.5s;
}

.rt_slide {
	flex: 0 0 100%;
	max-width: 100%;
	padding: 1rem;

}

img {
	max-width: 100%;
}

.rt_title {
	font-size: 2rem;
}